.news-container .items-container{max-width:50%}.news-header{margin-bottom:40px}.news-header h1{font-size:3em;margin-bottom:20px;color:#333}.news-header .news-subtitle{color:#666;font-size:1.1em;line-height:1.5;margin-bottom:30px}.news-timeline{margin-bottom:40px}.news-period{margin-bottom:35px}.news-period .period-header{font-size:1.3em;font-weight:bold;color:#5000ca;margin-bottom:15px;border-bottom:2px solid #5000ca;padding-bottom:5px}.news-period .period-items{list-style:none;padding:0;margin:0}.news-period .period-items .news-item{margin-bottom:12px;padding-left:20px;position:relative;line-height:1.6;color:#444;font-size:1em}.news-period .period-items .news-item:before{content:"•";color:#5000ca;font-weight:bold;position:absolute;left:0;top:0}.news-period .period-items .news-item .news-link{color:#5000ca;text-decoration:none;font-weight:500}.news-period .period-items .news-item .news-link:hover{text-decoration:underline}.news-period .period-items .news-item.news-item-with-image{display:flex;align-items:flex-start;gap:15px;padding-left:20px;margin-bottom:20px}.news-period .period-items .news-item.news-item-with-image:before{display:none}.news-period .period-items .news-item.news-item-with-image .news-image{flex-shrink:0;width:120px;height:80px;border-radius:8px;overflow:hidden;box-shadow:0 2px 8px rgba(0,0,0,.1)}.news-period .period-items .news-item.news-item-with-image .news-image img{width:100%;height:100%;object-fit:cover;transition:transform .3s ease}.news-period .period-items .news-item.news-item-with-image .news-image:hover img{transform:scale(1.05)}.news-period .period-items .news-item.news-item-with-image .news-content{flex:1;line-height:1.6}.news-footer{text-align:center;margin-top:40px;padding-top:20px;border-top:1px solid #ddd}.news-footer .more-news-link{color:#5000ca;text-decoration:none;font-weight:500;font-size:1em}.news-footer .more-news-link:hover{text-decoration:underline}.dark-mode .news-header h1{color:#fff}.dark-mode .news-header .news-subtitle{color:#ccc}.dark-mode .news-period .period-header{color:#8a5cf6;border-bottom-color:#8a5cf6}.dark-mode .news-period .period-items .news-item{color:#ddd}.dark-mode .news-period .period-items .news-item:before{color:#8a5cf6}.dark-mode .news-period .period-items .news-item .news-link{color:#8a5cf6}.dark-mode .news-period .period-items .news-item .news-link:hover{color:#b19cd9}.dark-mode .news-period .period-items .news-item.news-item-with-image .news-image{box-shadow:0 2px 8px rgba(138,92,246,.2)}.dark-mode .news-period .period-items .news-item.news-item-with-image .news-content{color:#ddd}.dark-mode .news-footer{border-top-color:#444}.dark-mode .news-footer .more-news-link{color:#8a5cf6}.dark-mode .news-footer .more-news-link:hover{color:#b19cd9}@media screen and (max-width: 768px){.news-container{padding:5%}.news-header h1{font-size:2.5em}.news-period .period-header{font-size:1.2em}.news-period .period-items .news-item{font-size:.95em}}